body {
	background-color: #071e39;
	line-height: 1.4;
	font-size: 18px;
	@include roboto-reg;
	color: #fff; }

a {
	text-decoration: none;
	color: inherit;
	@include roboto-reg;
	@include trans; }
.title-h1 {
	@include roboto-light;
	font-size: 72px;
	line-height: 1; }
.title-h2 {
	line-height: 1;
	@include roboto-med;
	font-size: 48px; }

//buttons
%button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	border-radius: 3px;
	text-transform: uppercase;
	transition: background-color 0.3s;
	text-align: center; }
.btn {
	@extend %button;
	color: #fff;
	line-height: 1;
	background-color: #00aaff;
	@include roboto-bold;
	padding: 18px 25px;
	&:hover,
	&:focus {
		background-color: lighten(#00aaff, 10%); }
	&:active {
		background-color: darken(#00aaff, 5%); } }
.btn-primary {
	@extend %button;
	background-color: #ff1799;
	padding: 27px 60px;
	color: #fafafc;
	font-size: 20px;
	@include robotomono-bold;
	&:hover,
	&:focus {
		background-color: lighten(#ff1799, 5%); }
	&:active {
		background-color: darken(#ff1799, 5%); } }
.btn-secondary {
	@extend %button;
	line-height: 1;
	color: #0ca8fd;
	font-size: 20px;
	@include robotomono-bold;
	border: 2px solid #0ca8fd;
	border-radius: 37px;
	padding: 25px 70px;
	overflow: hidden;
	&:hover,
	&:focus {
		border-color: #fb1d95;
		.btn-secondary__in {
			right: 0; } }
	&__in {
		position: absolute;
		top: -2px;
		bottom: -2px;
		width: 100%;
		top: 0;
		right: 120%;
		background-color: #fb1d95;
		display: block;
		transition: right 0.3s; }
	&__text {
		position: relative;
		z-index: 2; } }
.btn-menu {
	@include hide-text;
	width: 28px;
	height: 20px;
	position: relative;
	overflow: hidden;
	top: 1px;
	background-color: transparent;
	display: inline-block;
	vertical-align: middle;
	margin-left: 14px;
	span {
		position: absolute;
		left: 0;
		right: 0;
		top: 50%;
		margin-top: -1px;
		height: 3px;
		background-color: #fff;
		display: block;
		transition: transform 0.2s;
		&::after,
		&::before {
			content: '';
			display: block;
			position: absolute;
			height: 3px;
			background-color: #fff;
			left: 0;
			right: 0; }
		&::before {
			top: -8px;
			transform-origin: top right;
			transition: transform 0.2s, width 0.2s, top 0.2s; }
		&::after {
			top: 8px;
			transform-origin: bottom right;
			transition: transform 0.2s, width 0.2s, bottom 0.2s; } }
	&.is-active {
		span {
			@include rotate(180);
			&::before,
			&::after {
				width: 40%; }
			&::before {
				transform: translateX(15px) translateY(9px) rotate(-45deg); }
			&::after {
				transform: translateX(14px) translateY(-9px) rotate(45deg); } } } }

//icons
.icon-check {
	font-size: 32px;
	fill: #fff; }
