.comments {
	font-size: 0;
	margin-top: 82px;
	&__item {
		width: 33.3333%;
		display: inline-block;
		vertical-align: top;
		padding: 0 50px; } }
.profile {
	max-width: 411px;
	min-height: 521px;
	display: block;
	margin: auto;
	padding: 45px 32px 50px;
	border: 2px solid #27344d;
	&__media {
		width: 112px;
		height: 112px;
		position: relative;
		overflow: hidden;
		margin: auto;
		border-radius: 50%;
		img {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto;
			max-width: 100%;
			max-height: 100%; } }
	&__name {
		font-size: 24px;
		margin-top: 20px; }
	&__site {
		font-size: 14px;
		color: #ff1799;
		margin-top: 10px;
		@include roboto-bold; }
	&__circle {
		margin-top: 32px;
		span {
			display: inline-block;
			vertical-align: top;
			border-radius: 50%;
			width: 7px;
			height: 7px;
			background-color: #fb1d95;
			+ span {
				margin-left: 23px; } } }
	&__desc {
		font-size: 16px;
		margin-top: 32px;
		@include roboto-light; } }
