.inner {
	position: relative;
	max-width: 1880px;
	margin: auto;
	padding: 0 15px;
	@include clr; }
.section1 {
	background-color: #7200d4;
	height: 100vh;
	min-height: 800px;
	.inner {
		height: 100%; } }
.logo {
	@include panton;
	font-size: 36px;
	line-height: 1;
	display: inline-block;
	vertical-align: middle; }
.entry {
	font-size: 0;
	&__item {
		display: inline-block;
		vertical-align: middle;
		+ .entry__item {
			margin-left: 40px; } }
	a {
		color: #fafafc;
		font-size: 18px;
		&:hover,
		&:focus {
			color: darken(#fafafc, 15%); } } }
.company {
	text-align: center;
	line-height: 1;
	max-width: 433px;
	&__logo {
		@include hide-text; }
	&__text {
		font-size: 32px;
		margin-top: 5px; }
	&__button {
		margin-top: 46px; }
	img {
		display: block;
		max-width: 100%;
		height: auto; } }
.columns {
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center; }
.section4 {
	background-color: #17233B;
	height: 100vh;
	min-height: 800px;
	position: relative;
	overflow: hidden;
	.inner {
		padding-top: 112px;
		padding-bottom: 112px; }
	.title-h1 {
		margin-bottom: 81px;
		text-align: center; } }
.advertiser {
	position: relative;
	z-index: 5;
	font-size: 0;
	margin-left: (316px / 1850px) * 100%; // делим на ширину контейнера минус padding
	&__col {
		display: inline-block;
		vertical-align: top;
		max-width: 384px;
		+ .advertiser__col {
			margin-left: 56px; } }
	&__btn {
		line-height: 1;
		padding: 27px 72px;
		margin: 56px 0 0 47px; } }
.list {
	&__item {
		&:not(:last-child) {
			margin-bottom: 22px; } }
	&__item_top-vertical {
		.list__item-icon,
		.list__item-text {
			vertical-align: top; } }
	&__item-icon,
	&__item-text {
		display: table-cell;
		vertical-align: middle; }
	&__item-text {
		padding-left: 20px;
		@include roboto-light;
		font-size: 18px;
		line-height: 1.2; } }
.diagram3 {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	height: 1218px;
	background: url(../img/diagrams/diagram3.png) no-repeat center bottom;
	background-size: contain; }
.section5 {
	background: #131d31 url(../img/bg-sec5.jpg) no-repeat center top;
	background-size: cover;
	.inner {
		padding-top: 110px;
		padding-bottom: 110px; } }
.offer {
	text-align: center;
	&__title {
		font-size: 48px;
		@include roboto-light;
		margin-top: 40px; }
	&__button {
		margin-top: 50px; } }
.section6 {
	text-align: center;
	background-color: #071E39;
	.inner {
		padding-top: 86px;
		padding-bottom: 97px; } }
